<template>
	<view>
		<canvas type="2d" hidpi :canvas-id="CanvasID" :style="{ width: canvasW + 'px', height: canvasH + 'px' }"></canvas>
	</view>
</template>

<script>
	export default {
		name:"poster",
		props: {
			CanvasID:{		//CanvasID 等同于 canvas-id
				Type:String,
				default:'PosterCanvas'
			},
			imgSrc:{		//展示图
				Type:String,
				default:''
			},
			QrSrc:{			//二维码
				Type:String,
				default:''
			},
			Title:{			//文本内容
				Type:String,
				default:''
			},
			TitleColor:{	//标题颜色
				Type:String,
				default:'#333333'
			},
			LineType:{		//标题显示行数		（注超出2行显示会导致画布布局絮乱）
				Type:[String,Boolean],
				default:true
			},
			PriceTxt:{		//价格值
				Type:String,
				default:'99.99'
			},
			PriceColor:{	//价格颜色
				Type:String,
				default:'#e31d1a'
			},
			// OriginalTxt:{	//原价值
			// 	Type:String,
			// 	default:'199.99'
			// },
			OriginalColor:{	//默认颜色（如原价与扫描二维码颜色）
				Type:String,
				default:'#b8b8b8'
			},
			Width:{			//画布宽度  (高度根据图片比例计算 单位upx)
				Type:String,
				default:750
			},
			CanvasBg:{		//canvas画布背景色
				Type:String,		
				default:'#ffffff'
			},
			Referrer:{		//推荐人信息
				Type:String,
				default:'quitter推荐给你'
			},
			ViewDetails:{	//描述提示文字
				Type:String,
				default:'长按或扫描识别二维码'
			}
		},
		data() {
			return {
				canvasW:0,
				canvasH:0,
				canvasImgSrc:'',
				ctx:null,
				
			};
		},
		methods:{
			// 绘制画布
			async OnCanvas(){
				const that=this;
				that.ctx = uni.createCanvasContext(that.CanvasID,this);
				const C_W = uni.upx2px(that.Width),  // canvas宽度
					C_P = uni.upx2px(30), // canvas padding 间距
					C_Q = uni.upx2px(150);	// 二维码或太阳码宽高
				let _strlineW = 0; //文本宽度
				let _imgInfo = await that.getImageInfo({imgSrc:that.imgSrc});
				let _QrCode = await that.getImageInfo({imgSrc:that.QrSrc});
				let r = [_imgInfo.Width,_imgInfo.height];
				let q = [_QrCode.Width,_QrCode.height];
				let imgW = C_W - C_P *2;
				if(r[0]!==imgW){
					r[1] = Math.floor((imgW / r[0])*r[1]);
					r[0] = imgW;
				}
				if(q[0] !== C_Q){
					q[1]=Math.floor((C_Q / q[0])*q[1]);
					q[0]=C_Q;
				}
				
				that.canvasW = C_W;
				that.canvasH = r[1]+q[1]+128;
				that.ctx.setFillStyle(that.CanvasBg) //canvas背景颜色
				that.ctx.fillRect(0,0,C_W,that.canvasH); //canvas画布大小
				
				// 添加图片展示
				that.ctx.drawImage(_imgInfo.path,C_P,C_P,r[0],r[1]);
			},
			
			// 获取图片信息
			async getImageInfo({imgSrc}){
				return new Promise((res,errs)=>{
					uni.getImageInfo({
						src:imgSrc,
						success(image) {
							res(image);
						},
						fail(err) {
							errs(err)
						}
						
					})
				})
			},
			
			//
			getNewImage(){
				const that=this;
				uni.canvasToTempFilePath({
					canvasId:that.CanvasID,
					quality:1,
					complete(res) {
						console.log(res.tempFilePath);
						that.$emit('success',res);
					}
				},this)
			}
		}
	}
</script>

